<template>
  <div style="display: flex; align-items: center;" id="top_tool_div">
    <a-dropdown-button @click="addItem" type="dashed">
      增加一组
      <template #overlay>
        <a-menu >
          <a-menu-item @click="fillLocalIp">
            填充ip
          </a-menu-item>
          <a-menu-item @click="clearResultData">
            清空结果区
          </a-menu-item>
        </a-menu>
      </template>
    </a-dropdown-button>
    <!-- <a-button type="dashed" id="add_item" @click="addItem">增加一组</a-button> -->
    <a-select
      mode="combobox"
      placeholder="预置命令"
      :options="this.commandOptions"
      v-model:value="this.selectCommand"
      style="width: 30%;"
      @select="onCommandSelect"
    >
    </a-select>
    <a-input v-model:value="this.freeCommand" laceholder="自由命令" />
    <a-dropdown-button @click="execFreeCommand" type="primary">
      执行
      <template #overlay>
        <a-menu >
          <a-menu-item v-for="(item, index) in items" :key="index" @click="execFreeCommandWithDevice(item.commandForm)">
            {{ item.title }}
          </a-menu-item>
        </a-menu>
      </template>
    </a-dropdown-button>
  </div>
  <a-divider v-show="resultData.length > 0">结果展示区</a-divider>
  <a-spin :spinning="spinning" v-show="resultData.length > 0">
    <a-timeline style="max-height: 300px; overflow:auto; padding-top: 5px;">
      <a-timeline-item v-for="(item, index) in resultData" :key="index" :color="item.color">
        <pre>{{ item.data }}</pre>
      </a-timeline-item>
    </a-timeline>
  </a-spin>
  <a-collapse v-model:activeKey="activeKey">
    <a-collapse-panel v-for="(item, index) in items" :key="index" :header="item.title">
      <a-form
        :model="item.commandForm"
        name="basic"
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 16 }"
        autocomplete="off"
      >
        <a-form-item
          label="客户端ip"
          name="ip"
          :rules="[{ required: true, message: '请输入客户端ip' }]"
        >
          <a-input v-model:value="item.commandForm.ip" />
        </a-form-item>

        <a-form-item
          label="端口"
          name="port"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.port" />
        </a-form-item>

        <a-form-item
          label="包名"
          name="packageName"
          :rules="[{ required: false, message: '' }]"
        >
          <!-- <a-input v-model:value="item.commandForm.packageName" /> -->
          <a-input-search
            v-model:value="item.commandForm.packageName"
            placeholder="包名"
            enter-button
            @search="fillActivePackageName(item.commandForm)"
          />
        </a-form-item>

        <a-form-item
          label="代理地址"
          name="proxyAddr"
          :rules="[{ required: false, message: '' }]"
        >
          <a-input v-model:value="item.commandForm.proxyAddr" />
        </a-form-item>

        <a-form-item :wrapper-col="{ offset: 4, span: 16 }">
          <a-space wrap>
            <a-button type="primary" html-type="submit" @click="sendHttpRequest(item.commandForm, 'setProxy', item.title)">设置代理</a-button>
            <a-button type="primary" html-type="submit" @click="sendHttpRequest(item.commandForm, 'delProxy', item.title)">清除代理</a-button>
            <a-button type="primary" danger html-type="submit" @click="sendHttpRequest(item.commandForm, 'stop', item.title)">终止进程</a-button>
            <a-button type="primary" danger html-type="submit" @click="sendHttpRequest(item.commandForm, 'clear', item.title)">清理缓存</a-button>
            <a-button type="primary" danger ghost @click="distoryItem(index)">关闭分组</a-button>
          </a-space>
        </a-form-item>
      </a-form>
    </a-collapse-panel>
  </a-collapse>
</template>

<script>
import { message } from 'ant-design-vue';
import { ref } from 'vue';
import { GET, POST } from '@/utils/http'
const defaultCommandForm = {
      ip: "",
      port: "5555",
    }
const host = ""
export default {
  data() {
    return {
      activeKey: ref(['0']),
      // 命令表单组
      items: [
        {
          title: 'adb控制',
          inputValue: '',
          commandForm: Object.assign({}, defaultCommandForm)
        }
      ],
      // 自由命令
      freeCommand: '',
      // 预置命令选项
      commandOptions: [
        {value: 'devices', label: "devices"},
        {value: 'disconnect', label: "disconnect"},
        {value: 'shell pm list packages -3', label: "pm list packages -3"},

      ],
      // 选中的预置命令
      selectCommand: null,
      spinning: false,
      resultData: [],
    };
  },
  methods: {
    onCommandSelect(value) {
      this.freeCommand = value
      this.selectCommand = null
    },
    sendHttpRequest(commandForm, op, title) {
      commandForm.op = op
      this.spinning = true
      var showCmd = this.dealShowCommandInfo(commandForm, title)
      POST(host + '/adb', commandForm)
        .then(res => {
          if (res.data.data == "") {
            message.info("执行成功！")
            this.resultData.unshift({
              color: "green", 
              data: showCmd + "：执行成功！"
            })
          } else {
            this.resultData.unshift({
              color: "blue", 
              data: showCmd + "\n" + res.data.data
            })
          }
          if (this.resultData.length > 50) {
            this.resultData.pop(); // 移除最旧的数据
          }
          // message.info({
          //   content: () => {
          //     return h("pre", {class: "shell-output"}, res.data.data.split("\n").map(line => {
          //       return h("p", {}, line)
          //     }))
          //   }
          // })
        }).catch(errMsg => {
          console.log(errMsg)
          this.resultData.unshift({
              color: "red", 
              data: showCmd + "\n" + errMsg
            })
        }).finally(() => {
          this.spinning = false
        })
    },
    dealShowCommandInfo(commandForm, title) {
      var showCmd = commandForm.cmd
      if (commandForm.op != "free") {
        showCmd = commandForm.op
      }
      if (title) {
        showCmd += `(${title})`
      }
      return "命令 " + showCmd
    },
    addItem() {
      if (this.items.length >= 5) {
        message.error("已达到最大数量！")
        return
      }
      const newTitle = prompt('输入标题');
      if (newTitle) {
        this.items.push({
          title: newTitle,
          inputValue: '',
          commandForm: Object.assign({}, defaultCommandForm)
        });
      }
    },
    distoryItem(index) {
      // 移除items中对应index
      if (this.items.length <= 1) {
        message.error("至少保留一个分组！")
        return
      }
      this.items.splice(index, 1)
    },
    execFreeCommand() {
      this.sendHttpRequest({cmd: this.freeCommand}, "free")
    },
    execFreeCommandWithDevice(commandForm) {
      if (commandForm.ip == "") {
        message.error("所选分组设备ip不可为空！")
        return
      }
      commandForm.cmd = `-s ${commandForm.ip}:${commandForm.port} ${this.freeCommand}`
      this.sendHttpRequest(commandForm, "free")
    },
    fillLocalIp() {
      const proxyPort = prompt('输入端口号');
      GET(host + '/ip')
        .then(res => {
          // 遍历items，填充ip
          this.items.forEach(item => {
            item.commandForm.proxyAddr = res.data.data + ":" + proxyPort
          })
        }).catch(errMsg => {
          console.log(errMsg)
        })
    },
    fillActivePackageName(commandForm) {
      commandForm.op = 'getPackage'
      POST(host + '/adb', commandForm)
        .then(res => {
          commandForm.packageName = res.data.data
        })
    },
    clearResultData() {
      this.resultData = []
    }
  }
};
</script>

<style scoped>
#top_tool_div {
  margin-bottom: 20px;
}
#add_item {
  width: 20%;
}
.shell-output {
  white-space: pre-wrap;
  text-align: left;
}
</style>